.contactsBlock{
    background-color: #b8ebf6;
}

.contactsContainer{
    flex-direction: column;
    justify-content: flex-start;
}

.title{
    background-color: #6e939f;
}

.contactsForm {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ccc0c0;
}

.contactsForm input, .contactsForm textarea{
    width: 100%;
}

.submitBtn{
    background-color: #d5cdcd;
    padding: 10px 20px;
}